import * as React from 'react';
import './App.css';

const logo = require('./volunteer.svg');

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">CONSENSUS</h1>
          <h4 className="sub-title">Uma nova forma de assemblia</h4>
        </header>
        <section className="centered">
          <article className="description">
            <p className="p-text">
              Consensus  um projeto <i> <a target="_blank" href="https://opensource.org/about"> Open Source </a></i> licenciado sob a <i title="GNU"> <a target="_blank" href="https://www.gnu.org/licenses/gpl-3.0.pt-br.html">GNU General Public License</a> </i> destinado s pessoas que possam e queiram contribuir para uma nova forma de gerar debates, assemblias, reunies e deliberaes.  
            </p>
            <p className="p-text">
              Uma das maiores dificuldades em realizar assemblias de pessoas para deliberar ou debater determinado assunto  o esforo logstico que deve ser empreendido, quanto maior o nmero de pessoas, maior  esse esforo. <br/>
              Muitas vezes no s o esforo logstico  necessrio, mas tambm um esforo de organizao e gesto de conflitos durante a assemblia, pois frequentemente os participantes no respeitam o tempo de fala dos outros e/ou no conseguem manter suas falas conectadas com a pauta proposta.
            </p>
          </article>
        </section>
        <section className="centered">
          <p className="p-text">
            A plataforma pretende permitir que um usurio crie uma sala, envie o link para os participantes e ento configure as regras da assemblia como quiser; os participantes podem, durante seu tempo de fala, expor suas argumentaes a respeito de cada tpico da pauta.<br/>
            Por fim, os participantes votam nas falas mais relevantes e ento pode-se ter uma deliberao a partir dos resultados das argumentaes.
          </p>
        </section>
        <section className="centered">
          <p className="p-text">
            A plataforma ser composta por dois subprojetos, o primeiro  o desenvolvimento do <i>front end</i>, o segundo  o desenvolvimento do back end. Ambos sero desenvolvidos em JavaScript usando ReactJS para o primeiro e NodeJS para o segundo.<br/>
            Para colaborar com o projeto acesse os repositrios: <i> <a target="_blank" href="https://github.com/zscaiosi">front-end</a></i> e <i> <a target="_blank" href="https://github.com/zscaiosi">back-end</a></i>
          </p>
        </section>
        <section className="centered">
          <p className="p-text">
            Para participar das Sprints do projeto acesse: <i> <a target="_blank" href="https://docs.google.com/spreadsheets/d/19Uyy6afp8vrsGuWNZYCBTnUm_rYU2iD3beHBg0xfgEU/edit?usp=sharing">Google Sheets</a></i>
          </p>
        </section>
      </div>
    );
  }
}

export default App;



